@import './variables.sass';
@import './transitions.sass';

.contact-page-wrapper {
	background-attachment: fixed;
	width: 100vw;
	max-width: 100%;
	height: 100vh;
	background-color: $purple;
	animation: mount2 .3s linear forwards;
	transform-origin: top left;
	h3 {
		width: 100%;
		height: 40px;
		line-height: 40px;
		margin: 0; }
	section.content {
		max-height: calc(100vh - 162px);
		max-width: 100%;
		flex-direction: row;
		align-items: flex-start;
		align-content: center;
		justify-content: center;
		display: flex;
		flex-wrap: wrap;
		.item {
			height: 36%;
			margin: 5px;
			width: 46%;
			opacity: 0;
			background-color: rgba(255,255,255,.3);
			animation: mount .5s linear 1s forwards;
			color: $beige;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: space-around;
			padding: 10px;
			&-cv {
				flex-direction: row;
				width: calc(92% + 10px);
				height: 20%;
				margin: 5px 0; }
			&__text {
				background-color: $beige;
				text-align: right;
				padding: 15px 25px;
				border-radius: 5px;
				font-size: 20px;
				box-shadow: 0 0 6px 2px rgba(255,255,255,.4); }
			svg {
				fill: $beige; }
			&:hover {
				cursor: pointer;
				background-color: rgba(255,255,255,.8);
				svg {
					fill: $purple; }
				.item__text {
					background-color: $purple;
					color: $beige; } } } } }

@media screen and (max-width: 720px) {
	.contact-page-wrapper {
		h3 {}
		section.content {
			.item {
				&__text {
					font-size: 16px;
					padding: 8px 15px; } } } } }


@media screen and (max-width: 540px) {
	.contact-page-wrapper {
		padding-top: 90px;
		h3 {
			font-size: 18px; }
		section.content {
			.item {
				width: 100%;
				height: 20%;
				flex-direction: row;
				justify-content: space-between;
				&__text {
					font-size: 16px;
					text-align: right;
					padding: 15px 25px;
					width: 270px; }

				svg {
					height: 40px; } } } } }
